<template>
    <div class="main-page">
        <List ref="list" :apis="apis" :render="render" :fieldToPage="fieldToPage" FormCode="oa_car_check" aePageUrl="/modular/oa/CarsCheckAE">
            <van-button slot="contentRight" type="primary" @click="auditAny">批量审核</van-button>
            <van-button slot="contentRight" type="primary" @click="append">新增</van-button>
        </List>
    </div>
</template>

<script>
export default {
    name: "CarsCheckList",
    props: {},
    components: {
        List: () => import("@/universalPage/list/List.vue"),
    },
    data() {
        return {
            apis: {
                edit: this._api.VMS_Car_CheckEdit,
                create: this._api.VMS_Car_CheckCreate,
                list: this._api.VMS_Car_CheckManage,
                delete: this._api.VMS_Car_CheckDelete,
                audit: this._api.VMS_Car_CheckAudit,
                details: this._api.VMS_Car_CheckDetails,
                manage: this._api.VMS_Car_CheckManage,
            },
            fieldToPage: {
                FName: this.toDetails,
            },
            tableData: [],
        };
    },
    methods: {
        render(h) {
            return (
                <div>
                    <el-table-column
                        prop="FName"
                        label="标题"
                        min-width="100"
                        align="left"
                        header-align="center"
                    ></el-table-column>
                    <el-table-column
                        prop="FCarNo"
                        label="车辆名称"
                        width="100"
                        align="center"
                    ></el-table-column>
                    <el-table-column
                        prop="CreateName"
                        label="创建人"
                        width="100"
                        align="center"
                    ></el-table-column>
                    <el-table-column
                        prop="CreateTime"
                        label="创建时间"
                        width="160"
                        align="center"
                    ></el-table-column>
                </div>
            );
        },
        customRender(h) {
            return (
                <el-table
                    data={this.tableData}
                    highlight-current-row
                    stripe
                    border
                    height="400px"
                    empty-text=" "
                >
                    <el-table-column type="selection" width="50" align="center" />
                    <el-table-column
                        fixed
                        type="index"
                        label="序号"
                        width="50"
                        align="center"
                    />

                    <el-table-column
                        prop="FCarNo"
                        label="车牌号"
                        width=""
                        align="center"
                        width="100"
                    ></el-table-column>
                    <el-table-column
                        prop="FAppearance"
                        label="车辆外观"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FAppearance)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FAppearance")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FAppearance")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FCockpit"
                        label="驾驶舱"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FCockpit)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCockpit")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCockpit")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FEngineHood"
                        label="引擎盖内"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FEngineHood)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FEngineHood")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FEngineHood")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FMeter"
                        label="三检表"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FMeter)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FMeter")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FMeter")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FExtinguisher"
                        label="灭火器检查表"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FExtinguisher)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FExtinguisher")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FExtinguisher")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FVehicleSystem"
                        label="车辆系统"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FVehicleSystem)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FVehicleSystem")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FVehicleSystem")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FTyre"
                        label="车辆轮胎"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FTyre)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FTyre")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FTyre")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FLicenseTag"
                        label="车辆牌照及附件"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FLicenseTag)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FLicenseTag")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FLicenseTag")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FRunningStatus"
                        label="车辆运行"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FRunningStatus)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FRunningStatus")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FRunningStatus")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FCamera1"
                        label="驾驶舱摄像头1"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FCamera1)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCamera1")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCamera1")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FCamera2"
                        label="驾驶舱摄像头2"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FCamera2)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCamera2")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCamera2")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FCamera3"
                        label="车厢内摄像头"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FCamera3)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCamera3")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FCamera3")}
                                />
                            );
                        }}
                    ></el-table-column>
                    <el-table-column
                        prop="FOthers"
                        label="其他情况"
                        width=""
                        align="center"
                        formatter={(row) => {
                            return Boolean(Number(row.FOthers)) ? (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="color:#67c23a;font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FOthers")}
                                />
                            ) : (
                                <i
                                    class="el-icon-circle-check hand"
                                    style="font-size:20px"
                                    onClick={(e) => this.changeStatus(row, "FOthers")}
                                />
                            );
                        }}
                    ></el-table-column>
                </el-table>
            );
        },
        toDetails(row) {
            this.$refs.FormDetails.start(row);
        },
        auditAny() {
            let list = this.$refs.list.chooseList;
            if (list.length !== 0) {
                this.$u
                    .confirm("请确认批量审核!")
                    .then(() => {
                        this.apis
                            .audit(
                                list.map((it) => {
                                    return {
                                        Id: it.Id,
                                        FStatus: 3,
                                    };
                                })
                            )
                            .then((res) => {
                                if (res) {
                                    this.$u.msg("审核成功!");
                                    this.$refs.list.getList();
                                }
                            });
                    })
                    .catch(() => { });
            }
        },
        changeStatus(row, fieldName) {
            row[fieldName] = row[fieldName] ? 0 : 1;
        },
        append() {
            this.$refs.FormAE.start(false);
            this.VMS_CarIndex();
        },
        VMS_CarIndex() {
            this._api.VMS_CarIndex().then((res) => {
                if (res) {
                    this.tableData = res.data.map((it) => {
                        return {
                            FCarNo: it.FCarNo,
                            CarId: it.Id,
                            FAppearance: 1,
                            FCockpit: 1,
                            FEngineHood: 1,
                            FMeter: 1,
                            FExtinguisher: 1,
                            FVehicleSystem: 1,
                            FTyre: 1,
                            FLicenseTag: 1,
                            FRunningStatus: 1,
                            FCamera1: 1,
                            FCamera2: 1,
                            FCamera3: 1,
                            FOthers: 1,
                        };
                    });
                }
            });
        },
        submitBefore(info) {
            info.EntryList = this.tableData;
        },
        loadAfterFun(info) {
            this.tableData = info.EntryList;
        },
    },
    mounted() { },
};
</script>

<style lang="scss" >
</style>